<template>
    <div class="col-md-4 col-lg-3" >
        <!-- view tasks -->
        <div class="card" v-if="!update">
            <div class="card-header d-flex justify-content-between align-items-start">
                
                <div>
                    <p>{{t.task}}</p>
                    <small>{{t.date}}</small>
                </div>
                <div class="d-flex ml-3">
                    <button @click="changeState(true)" class="btn btn-warning btn-sm" title="como pueden ver en esta pueden editar esta tarea para cambio por otra mucho mejor, tu lo decides">!</button>
                    <button title="remove this task !" @click="emitRemoveTask(t._id)" class="btn btn-danger btn-sm">x</button>
                </div>
            </div>
        </div>

        <!-- edit tasks -->
        <div class="card" v-else>
            <div class="card-header">
                <form @submit.prevent="emitUpdateTask(t)">
                    <input class="form-control" v-model="editEle">
                    <div class="d-flex justify-content-center" >
                        <button type="submit"  class="btn btn-primary btn-sm">save</button>
                        <button @click="changeState(false)" class="btn btn-warning btn-sm">x</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import eventBus from '../eventBus'
export default {
    beforeMount(){
        eventBus.$on('update:ok', (res)=>{
            this.update = false
        })
    },
    props:['t'],
    data(){
        return{
            update: false,
            editEle: this.t.task
        }
    },
    methods:{
        changeState(val){
            this.update = val;
        },
        emitUpdateTask(task){
            let data = {
                id  :task._id,
                task: this.editEle
            }
            eventBus.$emit('task:update', data)
        },
        emitRemoveTask(id){
            eventBus.$emit('task:remove', id)
        }
    }
}
</script>

