import DataStore from 'nedb'

const db = new DataStore({filename:'data.db', autoload: true});


export class TaskModel 
{
    insert(task)
    {
        db.insert({task: task, date: new Date()}, (err, newrec)=>{
            if (err) {
                return err
            }
            console.log(newrec);
        })
    }

    async loadData()
    {
        let dac = 'vaciokl'
        await db.find({}, (err, docs) =>{
            if(err){
                
                return err;
            }
            /* console.log('se ejecuto ->',docs) */
            dac = docs;
        })
        console.log('dac = ', dac)
        return dac

    }
}