<template>
    <div class="container">
        <div class="row ">
            <h1 class="text-center w-100">Add your Tasks</h1>
            <hr>
        </div>
		<div class="row justify-content-center">
			<div class="col-lg-6">
        		<form @submit.prevent="addTask()" >
        			<div class="form-group d-flex">
        				<input type="text" placeholder="add your task here!" v-model="newTask" class="form-control">
        				<button class="btn btn-success">+</button>
        			</div>
        		</form>
        	</div>
		</div>
		<hr>
        <div class="row ">
        	<task v-for="task in tasks" :t="task" :key="task._id"/>
        </div>
    </div>
</template>

<script>
import Task from './Task.vue'
import DataStore from 'nedb'
import {TaskModel} from '../models/Task'
import eventBus from '../eventBus'

const db = new DataStore({filename:'data.db', autoload: true});
const task5 = new TaskModel()

export default {
	created(){
		this.loadData()
	},
	beforeMount(){
		
		
		eventBus.$on('task:update', (data)=>{
			this.updateTask(data);
		})
		eventBus.$on('task:remove', (id)=>{
			this.removeTask(id)
		})
		
	},
	components:{Task},
	data(){
		return{
			tasks:'',
			newTask: ''
		}
	},
	methods:{
		async addTask(){
			if(this.newTask === ''){
				 return false
			}
			db.insert({task: this.newTask, date: new Date()}, (err, newrec)=>{
            if (err) {
                return err
			}
			this.newTask = '';
			this.loadData()
            console.log(newrec);
        })
	
		},
		loadData(){
			/* task5.loadData().then((data)=>{
				this.tasks = data
			}) */
			db.find({}, (err, docs) =>{
				if(err){
					
					return err;
				}
				console.log('se ejecuto ->',docs)
				this.tasks = docs
			})
		},
		updateTask(data){
			db.update({_id: data.id}, {$set: {task: data.task}}, (err, docUpdate)=>{
				if(err){
					return err;
				}
				this.loadData()
				eventBus.$emit('update:ok', 'ok')
				console.log(docUpdate)
			})
		},
		removeTask(id){
			db.remove({'_id':id}, (err, rem)=>{
				if(err){
					return err;
				}
				this.loadData()
				console.log(rem);
			})
		}
	}


}
</script>

